import React, { PureComponent } from "react";
import ReactHtmlParser from 'react-html-parser';
import { reactStringReplace } from "../../../../constants";
import { STRING_TO_URL_CONVERT } from "../../../../utils";
export default class Content extends PureComponent {
  // constructor(props) {
  //   super(props);
  //   this.myRef = React.createRef();
  //   this.state = {width : null}
  // }
  // componentDidUpdate(){
  //   setTimeout(() => {
  //     // console.log('width', this.myRef && this.myRef.current ? this.myRef.current.clientWidth : null)
  //     if(this.myRef && this.myRef.current) this.setState({width: this.myRef.current.clientWidth})
  //   }, 1000);
  // }
  // componentDidMount(){
  //   setTimeout(() => {
  //     // console.log('width', this.myRef && this.myRef.current ? this.myRef.current.clientWidth : null)
  //     if(this.myRef && this.myRef.current) this.setState({width: this.myRef.current.clientWidth})
  //   }, 1000);
  // }
  render() {    
    const { wall, item,  isHover, cardWidth } = this.props; 
    const ts_ms_card_content ={fontFamily: wall.ThemeRule.css_font, fontSize:wall.ThemeRule.fontSize, color: isHover && wall.ThemeRule.fontColor === "#ffffff" ? "#000000" :    !isHover && wall.ThemeRule.fontColor === "#000000" ? "#ffffff" : wall.ThemeRule.fontColor}
    let content = STRING_TO_URL_CONVERT(item.content);
   const hashTag = { color: wall.Personalization.hashtag_color, fontWeight: "bold" }
  //  var cardCSS = {}
   console.log('this.props.cardWidth', this.props.cardWidth)
  //  if(cardWidth > 700){
  //    cardCSS = {  WebkitLineClamp: 6}
  //  }
   const cardCSS = cardWidth <= 700 && cardWidth > 250 ? {WebkitLineClamp: 6} : cardWidth <= 250 && cardWidth > 200 ? {WebkitLineClamp: 5} : cardWidth <= 200 ? {WebkitLineClamp: 4} : {}

    return (<>
         <div className={`ts_ms_card_content tb-cTSfont-${wall.ThemeRule.font_varient}`} style={{...ts_ms_card_content, ...cardCSS}} > 
         {
          wall.Personalization.hashtag_highlight !== 0 ?
            wall.Personalization.hashtag_all === 1 ? reactStringReplace(ReactHtmlParser(content), /#(\w+)/g, (match, i) => (<div className="tb_ms_hash_tag"  key={match + i} style={hashTag}>#{match}</div>))
              :wall.Personalization.hashtag_feed === 1 ? reactStringReplace(ReactHtmlParser(content), item.hash.hashString, (match, i) => (<div className="tb_ms_hash_tag"  key={match + i} style={hashTag}>{match}</div>)) 
                : ReactHtmlParser(content)
                 : ReactHtmlParser(content)
         }
         {/* {item.content} */}
          </div>
      </>
    );
  }
}
