import React, { PureComponent } from "react";
import { v4 as uuidv4 } from 'uuid';
import Card from "./Card";

export default class Row extends PureComponent {

      

    render() {
        const { dataArr, indexNo, completeDataObject, wall, webFilters, languageSetting, cardWidth, largeDiv, smallDiv } = this.props;
        return (
            <div className="ts_ms_post_row">
                {(dataArr && dataArr.length > 0) ?
                            
                                (dataArr && dataArr.length > 0) ? dataArr.map((item, index) => {
                                    const cardData = completeDataObject[item];
                                    return (<Card itemData={cardData} cardWidth={cardWidth}  fullWidth={false}  itemId={cardData.id} key={`post_idMs_${index}`} {...this.props} />)
                                }) : null
                            
                     : ''}

{/* <Card itemData={cardData}  key={`post_id_${index}`} itemIndex={index} {...this.props} /> */}
            </div>
        );
    }
}
