import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux';

export default function Hotspot(props) {
  const ctaSetting = useSelector(state => state.appData.ctaSetting)


  const { ugc_products, UgcSetting, item, isPopUp = false } = props;
  const blobStyleBg = { background: ctaSetting ? ctaSetting?.hotspot_color : null }
  const blob2StyleBg = { background: ctaSetting ? ctaSetting?.hotspot_color : null , color: ctaSetting ? ctaSetting?.hotspot_type_color : null }
  //const [sHotspot, setSHotspot] = useState(useSelector(state => state.modalPop))


 
  const prevent=(fn, defaultOnly)=> {
    return (e, ...params) => {
      e && e.preventDefault()
      !defaultOnly && e && e.stopPropagation()
      fn(e, ...params)
    }
  }
  const sH = (useSelector(state => state.modalPop))

  

  const openUrl = (url) => event => {
    if(parseInt(ctaSetting?.tab_target) == 2) window.parent.location.href = url
    else window.open(url, '_blank');
  }
  // console.log(ugc_products,UgcSetting)

  return (
    <div className="ts__post_hotspot">
      {ugc_products.map((data, index) => {
        let topp = parseInt(data.hotspot_cordinates.top);
        let leftt = parseInt(data.hotspot_cordinates.left);
        const org_price = data.UgcProduct.product_discount > 0 ? ctaSetting?.discount_price_color : ctaSetting?.original_price_color;
        let blobStyle = 
        {
        top: topp < `30` ? '18px' : topp < `70` && topp > `30` ? '18px' : 'auto',
        left: leftt < `30` ? '18px' : leftt < `70` && leftt > `30` ? '-25px' : 'auto',
        bottom: topp < `30` ? 'auto' : topp < `70` && topp > `30` ? 'auto' : '18px',
        right: leftt < `30` ? 'auto' : leftt < `70` && leftt > `30` ? 'auto' : '15px'
        }

     
        return (
          <div className="ts_st_post_hotspot_icon" key={index} style={{ top: data.hotspot_cordinates.top, left: data.hotspot_cordinates.left }} onClick={prevent(() => console.log("Child Element!"))}>
            {ctaSetting?.hotspot_type==1 ?
              <>
                <div onClick={openUrl(data.UgcProduct.product_url)} className="ts_st_post_hotspot_icon_blob_outer ts_st_post_numberHotspot">
                  {(sH.showAll || !sH.isShowPopUp) || (sH.showHotspot && (sH.index == index && sH.hotspotId == data.id)) ? <div className="ts_st_post_hotspot_icon_blob_custom" id={`ts_st_post_hotspot_icon_blob${index}`} style={{ ...blob2StyleBg, textAlign: 'center' }}> {index + 1} 
                  </div> : null}
                </div>
                {(sH.showAll|| !sH.isShowPopUp) || (sH.showHotspot && (sH.index == index && sH.hotspotId == data.id)) ? <div className={`${!sH.showAll ? 'ts_st_post_hotspot_icon_hover_custom ' : 'ts_st_post_hotspot_icon_hover'} ${!sH.isShowPopUp ? 'ts_st_post_hotspot_icon_hover' : '' }`} style={blobStyle} >
                  <div className="ts_st_post_hotspot_icon_hover_p_name">{data.UgcProduct.product_title}</div>
                  <div style={{ color: org_price }} className={`ts_st_post_hotspot_icon_hover_act_price ${data.UgcProduct.product_discount > 0 ? `ts_st_post_hotspot_icon_hover_act_prise_diabled` : ''}`}>{data.UgcProduct.price_currency_symbol} {' '}{Math.trunc((data.UgcProduct.product_price)*100)/100}</div>
                  {data.UgcProduct.product_discount > 0 ?
                    <div className="ts_st_post_hotspot_icon_hover_dis_price" style={{ color: ctaSetting?.original_price_color }}>{data.UgcProduct.price_currency_symbol} {' '}{Math.trunc((data.UgcProduct.product_discount)*100)/100}</div> : ''}
                </div> : null}
              </>
              :
              <>
                <div onClick={openUrl(data.UgcProduct.product_url)} className="ts_st_post_hotspot_icon_blob_outer">
                  <div className="ts_st_post_hotspot_icon_blob" style={blobStyleBg}>
                    <div></div>
                  </div>
                </div>
                <div className={`ts_st_post_hotspot_icon_hover `} style={blobStyle} >
                  <div className="ts_st_post_hotspot_icon_hover_p_name">{data.UgcProduct.product_title}</div>
                  <div style={{ color: org_price }} className={`ts_st_post_hotspot_icon_hover_act_price ${data.UgcProduct.product_discount > 0 ? `ts_st_post_hotspot_icon_hover_act_prise_diabled` : ''}`}>{data.UgcProduct.price_currency_symbol} {' '}{Math.trunc((data.UgcProduct.product_price)*100)/100}</div>
                  {data.UgcProduct.product_discount > 0 ?
                    <div className="ts_st_post_hotspot_icon_hover_dis_price" style={{ color: ctaSetting?.original_price_color }}>{data.UgcProduct.price_currency_symbol} {' '}{Math.trunc((data.UgcProduct.product_discount)*100)/100} </div> : ''}
                </div>
              </>
            }
          </div>

        )
      })}
    </div>
  )
}
