import React, { PureComponent } from "react";
import HotSpot from "../../Elements/HotSpot";
import { IMG_LOAD_AFTER_COMPLETE_LAYOUT, IS_SCREEN_SHOT, marginTop_mainDiv } from '../../../../utils'


export default class Image extends PureComponent {

  componentDidMount() {
    const resizeObserver = new ResizeObserver((entries) => {
      window.dispatchEvent(new Event('resize'));
      this.getMarginTop();
  })
  resizeObserver.observe(document.querySelector('.taggshopContainer'));

  }
  getMarginTop(){
    setTimeout(() => window.isEditor ?  marginTop_mainDiv(99) :'', 100)
  }
  
  render() {
    const { ImageClass, ImageUrl, item, ugc_products, wall ,width, height} = this.props
    const ImageBg = { backgroundImage: `url(${ImageUrl})` };
    const styleDiv ={ height: item.hotspot ? (width > height) ? `auto` : `100%`:(width > height) ? '100%':`auto`, width: item.hotspot ? (width > height) ? `100%`: `auto`:(width > height) ? 'auto':`100%`}
    const styleImg ={ height: (width > height) ? '100%':`auto`, width: (width > height) ? 'auto':`100%`}

    return <>
      <div className="ts_ms_image_blur_bg" style={ImageBg}><div></div></div>
      <div  className={ImageClass} style={styleDiv}>
        <img loading="lazy"  data-src={ImageUrl}  src={ImageUrl}  data-is-optmized="0"  data-link={item.link} data-load="0" data-wall-id={wall.Wall.id} data-item-id={item.id} onLoad={this.onLoadImage} data-filter-id={item.filterId} data-stories={item.stories} data-network={item.networkId}  width={width} height={height} className={`imgID${item.id}`} onError={(e) => { if(item.networkId !== `28`) IMG_LOAD_AFTER_COMPLETE_LAYOUT(e) }}  alt="image" style={styleDiv} />
        {item.hotspot ? <HotSpot ugc_products={ugc_products} UgcSetting={item.ugc_personalizaion.UgcSetting} item={item}/> :null }
      </div>
    </>
      ;
  }
}