import React, { PureComponent } from "react"; 

export default class CardIcons extends PureComponent {
    render() { 
    const {item, wall, cardWidth} = this.props;
    const isShopIcon = (item.ugc_products && item.ugc_products.length > 0 && item.hotspot === 0) ? true : false;  
    const isVideo = (item.type===3 || item.type === 5) ? true : false;  
    const loggedUserId = wall && wall.User && wall.User.id !==31724;
    const iconSize =wall && wall.ThemeRule && wall.ThemeRule.fontSize ? wall.ThemeRule.fontSize :'40px';
    const multiImages = (item.imageList && item.imageList.length > 0) ? true : false
    const carousal_shopable  = (multiImages && isShopIcon) ? true: false;
    const classThm55 = wall.Personalization.commerceTheme ==55 ? 'tb_cs_video_multiImage':'';
    //const multiclsAllThm = isShopIcon ? 'tb_cs_carousal_icon_bottom':'tb_cs_carousal_icon';
      const iconClass = cardWidth <= 700 && cardWidth > 500 ? { fontSize: '16px' }
        : cardWidth <= 500 && cardWidth > 350 ? { fontSize: '14px' }
          : cardWidth <= 350 && cardWidth > 250 ? { fontSize: '12px' }
            : cardWidth <= 250 && cardWidth >= 150 ? { fontSize: '11px' }
              : cardWidth < 150 && cardWidth > 100 ? { fontSize: '10px' } : 
              cardWidth <= 100 && cardWidth > 50 ? { fontSize: '8px' } : {}
    return (
      <div className="ts_post_iocn">
        <div className={`ts_social-icon ${classThm55}`} style={{ color: 'rgb(112, 112, 112)'}}> 
          {isVideo ? 
          <div className="ts_video_icon tb_cs_video_multiImage" style={iconClass}>
            <div className="tb__icon tb-video-alt"><div></div></div>
          </div>:''}
          {!isShopIcon  && loggedUserId && wall.Personalization.commerceTheme !==55 ?
          <div className="ts_shop_icon" style={iconClass}>
            {wall.User.id === 31665 ? <svg fill="#ffffff" height="22" viewBox="0 0 48 48" width="22"><path d="M34.6 6.1c5.7 0 10.4 5.2 10.4 11.5 0 6.8-5.9 11-11.5 16S25 41.3 24 41.9c-1.1-.7-4.7-4-9.5-8.3-5.7-5-11.5-9.2-11.5-16C3 11.3 7.7 6.1 13.4 6.1c4.2 0 6.5 2 8.1 4.3 1.9 2.6 2.2 3.9 2.5 3.9.3 0 .6-1.3 2.5-3.9 1.6-2.3 3.9-4.3 8.1-4.3m0-3c-4.5 0-7.9 1.8-10.6 5.6-2.7-3.7-6.1-5.5-10.6-5.5C6 3.1 0 9.6 0 17.6c0 7.3 5.4 12 10.6 16.5.6.5 1.3 1.1 1.9 1.7l2.3 2c4.4 3.9 6.6 5.9 7.6 6.5.5.3 1.1.5 1.6.5.6 0 1.1-.2 1.6-.5 1-.6 2.8-2.2 7.8-6.8l2-1.8c.7-.6 1.3-1.2 2-1.7C42.7 29.6 48 25 48 17.6c0-8-6-14.5-13.4-14.5z"></path>
            </svg> :
            <div className="tb__icon tb-shopping-bag"><div></div></div>}
          </div>:''}
          {!carousal_shopable && multiImages && wall.Personalization.commerceTheme !==55 ? <div className="tb__icon tb-multiple tb_cs_carousal_icon" style={iconClass}> </div> : ''}
          {!multiImages && wall.Personalization.commerceTheme ==55 ? <div className={`ts_cs_multiple_option`}> <div className={`tb__icon tb-multiple`} style={iconClass}> </div></div> : ''}
        </div>
        {carousal_shopable && wall.Personalization.commerceTheme !==55 ? <div className="tb__icon tb-multiple tb_cs_carousal_icon_bottom" style={iconClass} > </div> : ''}

      </div>
    );
  }
}
