import React, { PureComponent, Fragment } from "react";
import { themePostTracking } from '../../../../../../../../../../actions/themeActions/themeActions'
export default class SocialActions extends PureComponent {

    onSharePost = event => {
        const { item, wall } = this.props;
        if (event) { themePostTracking({ type: 2, action: 2, wall: wall.Wall.id, feed: item.feedId, post: item.referenceId, }) }
      }
    
    render() {
        const { item, wall, network } = this.props
        const networkColor = wall.ThemeRule.iconType == 505 ? network.color : wall.ThemeRule.iconColor;
        return (
            <div className="ts__card_socialAction" style={{ color: 'rgb(255, 255, 255)' }}>
                {item.networkId == 1 ?
                    <React.Fragment>
                        <a target="_blank"  onClick={this.onSharePost}  href={`https://twitter.com/intent/tweet?in_reply_to=${atob(item.postId)}`} title="View">
                            <div className="tb__icon tb-share-square" style={{ color: networkColor }}><div></div></div>
                        </a>
                        <a href={`https://twitter.com/intent/retweet?tweet_id=${atob(item.postId)}`} title="Comment" target="_blank" className="ts_icon_count_tagg">
                        <div className=" tb__icon" style={{ color: networkColor }}>
                        <svg xmlns="http://www.w3.org/2000/svg" fill={networkColor} viewBox="0 0 640 512"><path d="M614.2 334.8C610.5 325.8 601.7 319.1 592 319.1H544V176C544 131.9 508.1 96 464 96h-128c-17.67 0-32 14.31-32 32s14.33 32 32 32h128C472.8 160 480 167.2 480 176v143.1h-48c-9.703 0-18.45 5.844-22.17 14.82s-1.656 19.29 5.203 26.16l80 80.02C499.7 445.7 505.9 448 512 448s12.28-2.344 16.97-7.031l80-80.02C615.8 354.1 617.9 343.8 614.2 334.8zM304 352h-128C167.2 352 160 344.8 160 336V192h48c9.703 0 18.45-5.844 22.17-14.82s1.656-19.29-5.203-26.16l-80-80.02C140.3 66.34 134.1 64 128 64S115.7 66.34 111 71.03l-80 80.02C24.17 157.9 22.11 168.2 25.83 177.2S38.3 192 48 192H96V336C96 380.1 131.9 416 176 416h128c17.67 0 32-14.31 32-32S321.7 352 304 352z"/></svg>
                               
                                </div>
                                <div style={{ color: networkColor }}>{item.comment_count != 0 ? item.comment_count : ''}</div>
                        </a>
                        <a href={`https://twitter.com/intent/favorite?tweet_id=${atob(item.postId)}`} title="Like" target="_blank" className="ts_icon_count_tagg">
                            <div className="tb__icon tb-heart" style={{ color: networkColor }}><div></div></div>
                            <div style={{ color: networkColor }}>{item.like_count != 0 ? item.like_count : ''}</div>
                        </a>
                    </React.Fragment>
                    : (item.networkId == 2 || item.networkId == 3 || item.networkId == 7 || item.networkId == 8 || item.networkId == 10 || item.networkId == 18) ?
                     <React.Fragment>
                            
                            <a href={item.link} title="Like" target="_blank" className="ts_icon_count_tagg">
                                <div className="tb__icon tb-thumbs-up" style={{ color: networkColor }}>
                                    <div className="">{(item.networkId == 10 && item.feedId > 73590) || (item.networkId == 18) ? item.like_count : null}
                                    </div>
                                </div>
                            </a>
                            <a href={item.link} title="Comment" target="_blank" className="ts_icon_count_tagg">
                                <div className="tb__icon tb-comment-dots" style={{ color: networkColor }}><div></div><div >
                                    {(item.networkId == 10 && item.feedId > 73590) || (item.networkId == 18) ? item.comment_count : null}
                                    </div>
                                </div>
                            </a>
                     </React.Fragment>
                        : item.networkId == 4 ? <a href={item.link} className="fa fa-comment-o tooltips" title="Comment" target="_blank"><div></div></a> : null}
            </div>
        );
    }
}


