import React, { PureComponent } from "react";
import { themePostTracking } from '../../../../../../../../../../actions/themeActions/themeActions'
export default class CtaButton extends PureComponent {
  onClickCtAButton = event => {
    const { item, wall } = this.props;
    if (event) { themePostTracking({ type: 1, action: 2, wall: wall.Wall.id, feed: item.feedId, post: item.referenceId, }) }
  }
  render() {    
    const {shoppingText, ctaClass, styleCta } = this.props;
    return (
      
        <div className={ctaClass} style={styleCta}  onClick={this.onClickCtAButton} > {shoppingText} </div>
    );
  }
}
